import { key } from './meta';
import type { ConvertToPluginCommands, ConvertToPluginQueries, ConvertToPluginActionCreators, ConvertToPluginActionPredicates } from '@ali/4ever-cangjie';
import type { commands } from '../bi/commands';
import type { queries } from '../bi/queries';
import type { actions, isActions } from '../bi/actions';

export interface ColorToolbarLocale {
  buttonTitle: string;
  defaultText: string;
  customText: string;
  chooseText: string;
  bgColorText: string;
  clearText: string;
  colorAndHighlightTooltip: string;
}

export interface ColorToolbarConfig {
  locale?: ColorToolbarLocale;
}


declare module '@ali/4ever-cangjie' {
  /** type combination for commands、queries、actions */
  interface PluginCommands extends ConvertToPluginCommands<typeof commands>{}
  interface PluginQueries extends ConvertToPluginQueries<typeof queries>{}
  interface PluginActionCreators extends ConvertToPluginActionCreators<typeof actions>{}
  interface PluginActionPredicates extends ConvertToPluginActionPredicates<typeof isActions>{}

  /** 插件配置 */
  interface BiPluginConfig {
    [key]?: ColorToolbarConfig;
  }
  interface ZhiPluginConfig {
    [key]?: ColorToolbarConfig;
  }
  interface MobileBiPluginConfig {
    [key]?: ColorToolbarConfig;
  }
  interface MobileZhiPluginConfig {
    [key]?: ColorToolbarConfig;
  }
}
